@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
* {
  box-sizing: border-box;
  margin: 8px 0;
  padding: 0;
  font-family: 'Oswald', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #4190a2;
  color: #fadee3;
}

a:link {
  color: #fadee3;
}

a:visited {
  color: #A0A097;
}

.app-container {
  min-width: 360px;
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.app-container ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100% ;
  border-top: 1px solid #fadee3;
}

.app-container ul li {
  border: 1px solid #fadee3;
  border-top: none;
  margin: 0;
  padding: 4px;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.app-container ul li p {
  margin: 0;
  width: 90%;
}

.app-container ul li div {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 90%;
  margin-top: 0;
  font-size: 14px;
}

.app-container ul li div * {
  margin: 0;
}

.repository {
  font-size: 24px;
  font-weight: bold;
}

.under-repository-name {
  width: 90%;
  border: none;
  border-top: 1px solid #fadee3;
}

.search-container {
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.search-container input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 3px solid #fadee3;
  transition: 0.5s;
  outline: none;
}

.search-container input:focus {
  border: 3px solid #A0A097;
}

.search-container button {
  width: 100%;
}

.search-container button:hover {
  cursor: pointer;
}

.search-container button:active {
  outline: none;
}

.search-container button:focus {
  outline: none;
}

